:root {
  --card-box-shadow: 0 8px 17px 2px rgba(0,0,0,0.14), 0 3px 14px 2px rgba(0,0,0,0.12), 0 5px 5px -3px rgba(0,0,0,0.2);
  --hover-box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2);
}

body {
  font: normal 16px/150% Arial, Helvetica, sans-serif;
}

home {
  display: block;
  text-align: center;
}

.icon-group {
  padding: var(--universal-padding);
}

.icon-group.left {
  text-align: left;
}

.icon-group.right {
  text-align: right;
}

#node-aditionalinfo {
  width: 200px;
  resize: none;
}

.tooltip.no-border {
  border: none;
}

.tooltip.right:before {
  bottom: 20%;
  left: 101%;
  border-right-color: #212121;
  border-top-color: rgba(0, 0, 0, 0);
  transition: opacity 0.7s;
}

.tooltip.right:after {
  left: 100%;
  margin-bottom: 0;
  bottom: 50%;
  -webkit-transform: translate(calc(2 * var(--universal-margin)), calc(2 * var(--universal-margin)));
          transform: translate(calc(2 * var(--universal-margin)), calc(2 * var(--universal-margin)));
  transition: opacity 0.7s;
}

.card > .section.no-padding {
  padding: 0;
}

.input-group.no-margin > input {
  margin: 0;
}

.material-icons {
  cursor: pointer;
  opacity: 0.5;
  vertical-align: middle;
}

.material-icons:hover {
  opacity: 1;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.header .modal-title {
  display: inline;
}

.header .modal-close {
  float: right;
}

.header:after {
  clear: both;
}

.modal-arrow:before {
  content: '';
  background: transparent;
  border: var(--universal-margin) solid transparent;
  position: absolute;
  bottom: 40%;
  left: -8px;
  z-index: 1;
  border-right-color: #212121;
  border-top-color: rgba(0, 0, 0, 0);
  transition: opacity 0.7s;
  clip: auto;
  -webkit-clip-path: inset(0%);
          clip-path: inset(0%);
}

.modal .card {
  box-shadow: var(--card-box-shadow);
}

.modal .section label {
  line-height: 36px;
  padding: 0;
}

.toast {
  height: 1.5em;
  opacity: 0.7;
}

.toast.error {
  background-color: red;
}

.root-node {
  text-align: left;
  padding-left: var(--universal-margin);
}

.tree-node {
  line-height: 1;
}

.tree-node .node-content {
  cursor: pointer;
}

.tree-node .node-content:before {
  box-sizing: border-box;
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  height: 1.5em;
}

.tree-node > .node-children {
  padding-left: 1.5em;
}

.tree-node.expanded .node-toggle {
  content: "";
  display: block;
  position: relative;
  width: 0;
  height: 0;
  vertical-align: middle;
  margin-top: -6px;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-top: 6px solid black;
  cursor: pointer;
  margin-right: 0.5em;
  display: inline-block;
}

.tree-node.collapsed .node-toggle {
  content: "";
  display: block;
  position: relative;
  width: 0;
  height: 0;
  vertical-align: middle;
  margin-top: -6px;
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
  border-left: 6px solid black;
  margin-right: -6px;
  margin-right: 0.5em;
  cursor: pointer;
  display: inline-block;
}

.node-content:hover:before {
  border: 1px solid var(--card-border-color);
}

.node-content.selected {
  font-weight: bold;
}

.tree-node.collapsed > .node-children {
  overflow: hidden;
  max-height: 0;
  transition: max-height 0.3s ease;
}

.tree-node.collapsed > .node-children .node-content:before {
  height: 0;
}

